import React from "react"
import Layout from "../components/layout";
import SEO from "../components/seo"
import { Carousel, Row, Container, Card, Col } from "react-bootstrap";
import { Link } from "gatsby"
import ImageOne from '../images/10.jpg';
import ImageTwo from '../images/11.jpg';
import ImageThree from '../images/12.jpg';
import ImageFour from '../images/13.jpg';
import ImageFive from '../images/21.jpg';
import ImageSix from '../images/23.jpg';

const IndexPage = () => {

    return(
        <Layout>
            <SEO title="Home" />
            <div className="jumbotron">
                <div className="container">
                    <h1 className="display-5">Welcome!</h1>

                    <Carousel id='home-carousel'>
                        <Carousel.Item>
                            <img className="d-block w-100"
                                alt="View of Kit Hill"
                                src={ImageOne}
                            />
                        </Carousel.Item>
                        <Carousel.Item>
                            <img
                                className="d-block w-100"
                                src={ImageTwo}
                                alt="River Tamar"
                            />
                        </Carousel.Item>
                        <Carousel.Item>
                            <img
                                className="d-block w-100"
                                src={ImageThree}
                                alt="Hay bales in a field"
                            />
                        </Carousel.Item>
                        <Carousel.Item>
                            <img
                                className="d-block w-100"
                                src={ImageFour}
                                alt="Local cows"
                            />
                        </Carousel.Item>
                        <Carousel.Item>
                            <img
                                className="d-block w-100"
                                src={ImageFive}
                                alt="Dartmoor"
                            />
                        </Carousel.Item>
                        <Carousel.Item>
                            <img
                                className="d-block w-100"
                                src={ImageSix}
                                alt="A nearby field"
                            />
                        </Carousel.Item>
                    </Carousel>
                    <br/>
                    <p className="lead">
                        Rhylla Cottage is a spacious 1 bedroom cottage with modern facilities.
                    </p>
                    <p className="lead">
                        Situated in an elevated position within the Tamar Valley, Rhylla Cottage has wonderful views of the countryside and river valley below and offers a perfect stay for people who enjoy peace, tranquillity and cosy home from home comforts.
                    </p>
                </div>
            </div>

            <Container fluid>
                <Row>
                    <Col style={{marginBottom: 24}}>
                        <Card>
                            <Card.Body>
                                <Card.Title>Our accommodation</Card.Title>
                                <Card.Text>
                                    <p>Rhylla Cottage is ideally situated for those wanting an abundance of local walks, cycling trails and other activities.</p>
                                    <p>Rhylla Cottage is available to rent on a weekly basis or for short breaks.</p>
                                </Card.Text>
                                <Link to='/accommodation/rhylla-cottage' className='btn btn-primary btn-lg'>See what's available</Link>
                            </Card.Body>
                        </Card>
                    </Col>
                    <Col style={{marginBottom: 24}}>
                        <Card>
                            <Card.Body>
                                <Card.Title>Interested in staying or have a question?</Card.Title>
                                <Card.Text>
                                    <p>Get in contact with us and we'll respond as soon as possible!</p>
                                </Card.Text>
                                <Link to='/contact-us' className='btn btn-primary btn-lg'>Contact Us</Link>
                            </Card.Body>
                        </Card>
                    </Col>
                </Row>
            </Container>
    
   
        </Layout>
    )
};

export default IndexPage
